import React from 'react';
import { graphql } from 'gatsby';
import Img from 'gatsby-image';

import Layout from '../components/Layout';
import Banner from '../components/Banner';
import Container from '../components/Container';
import BlockHeaderNoImage from '../components/BlockHeaderNoImage';
import SpaceDivider from '../components/SpaceDivider';
import BlockCards from '../components/BlockCards';
import Footer from '../components/Footer';

import { edgesByName } from '../utils/gql';

export const pageQuery = graphql`
  {
    images: allFile(filter: { relativeDirectory: { eq: "integrations" } }) {
      edges {
        node {
          name
          childImageSharp {
            fixed(width: 240) {
              ...GatsbyImageSharpFixed
            }
          }
        }
      }
    }
  }
`;

export default function IntegrationsPage({ data }) {
  const images = edgesByName(data.images, node => node.childImageSharp.fixed);

  return (
    <Layout>
      <Banner />
      <Container>
        <BlockHeaderNoImage
          subtitle="Integrations"
          title="Integrate payment terms seamlessly into your checkout"
          desc="Our API supports flexible customizations, allowing you to offer terms in all of your existing sales channels."
          maxWidth="700"
          primaryButtonText="Get a Demo"
        />
        <SpaceDivider height="75" />
        <BlockCards
          title="Platform integrations"
          subtitle={<span />}
          hideRightArrow
          cards={[
            {
              id: 'magento-2',
              href:
                'https://docs.resolvepay.com/docs/platform-integrations/magento-2',
              title: '',
              content: (
                <div>
                  <div className="integration-logo">
                    <Img fixed={images['magento2-logo']} alt="Magento2" />
                  </div>
                  <br />
                  <br />
                  Magento is packed with B2B
                  <br />
                  features that drive growth and
                  <br />
                  has an open-source ecosystem.
                  <br />
                  The Resolve Extension allows
                  <br />
                  your business customers to
                  <br />
                  apply for terms and checkout
                  <br />
                  without leaving your online store.
                  <br />
                  <br />
                </div>
              ),
            },
            {
              id: 'resolve-direct-api',
              href: 'https://app.resolvepay.com/docs/api/',
              title: '',
              content: (
                <div>
                  <div className="integration-logo">
                    <Img
                      fixed={images['resolve-direct-api']}
                      alt="Resolve Direct API"
                    />
                  </div>
                  <br />
                  <br />
                  Resolve's flexible API can be
                  <br />
                  integrated into any platform
                  <br />
                  Allow your business customers to
                  <br />
                  apply for terms and checkout
                  <br />
                  without leaving your online store.
                  <br />
                  <br />
                </div>
              ),
            },
          ]}
          marginBottom
        />
      </Container>
      <Footer />
    </Layout>
  );
}
